.main-navigation {
  width: 100%;
  height: 4.5rem;
  background: #222a75;
  position: fixed;
  z-index: 1;
}

.main-navigation nav {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.main-navigation ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  height: 100%;
  align-items: center;
  width: 100%;
}

.main-navigation li {
  margin: 0 1rem;
}

.main-navigation a {
  display: block;
  padding: 0.5rem 1rem;
  text-decoration: none;
  color: white;
  border-radius: 5px;
}

.main-navigation a:hover,
.main-navigation a:active {
  background: rgb(227, 255, 235);
  color: #5465c5;
}

.web-streamer {
  color:#ebffba;
  font-size: 25px;
}
.web-streamer:hover {
  color:#c9ff65;
  font-size: 30px;
}

.home_icon {
  justify-content: flex-start;
  padding-left: 30px !important;
}

.my_content {
  justify-content: flex-end;
  padding-right: 20px !important;
}

.header_title {
  justify-content: center;
  padding-right: 80px !important;
}
