.row {
    margin: 0px 20px;
    max-width: 100%;
    color: #fff;
  }

.row li {
    margin: 10px;
    list-style: none;
}
  
  .row_poster {
    object-fit: contain;
    width: 100%;
    max-height: 150px;
    max-width: 252px;
    transition: transform 450ms;
    margin-right: 10px;
    border-radius: 10px;
    border-color: rgb(218, 77, 77);
    box-shadow: 0 4px 8px 0 rgba(255, 254, 254, 0.2), 0 6px 20px 0 rgba(255, 209, 209, 0.19);
  }

  .row_poster:last-of-type {
      /* margin-right: 100px; */
  }
  
  .row_posters {
    /* list-style: none; */
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
  }
  
  .row_posters::-webkit-scrollbar {
    display: none;
  }
  
  .row_poster:hover {
    transform: scale(1.08);
  }
  
  .row_posterLarge {
    max-height: 265px;
  }
  
  .row_posterLarge:hover {
    transform: scale(1.09);
  }
  
